body {
  font-family: Poppins, sans-serif;
  background-color: #f6f6f6;
  font-weight: 400; }

#return-to-top {
  bottom: 0;
  right: 0;
  position: fixed;
  width: 64px;
  height: 64px;
  z-index: 10;
  cursor: pointer;
  display: none;
  background-color: #fff;
  border-top-left-radius: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../img/icons/UP.svg); }

.wrapper {
  width: 100%;
  max-width: 1074px;
  margin: 0 auto; }

.logo {
  display: inline-block;
  width: 107px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: contain;
  background-image: url(../img/logo.png); }

.breadcrumbs {
  display: flex;
  align-items: center;
  margin: 24px 0 48px; }
  .breadcrumbs__item {
    display: flex;
    align-items: center; }
    .breadcrumbs__item:after {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
      margin: 0 8px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      background-image: url(../img/icons/i_breadcrumbs_arrow.svg); }
    .breadcrumbs__item:last-child:after {
      display: none; }
  .breadcrumbs__link {
    color: #91A0B6;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px; }
    .breadcrumbs__link:hover {
      text-decoration: underline; }

.search {
  background-color: rgba(4, 191, 148, 0.16);
  border-radius: 16px;
  padding: 45px 60px 58px; }
  .search__title {
    color: #04BF94;
    font-weight: 800;
    font-size: 32px;
    line-height: 48px;
    margin-bottom: 19px; }
  .search__text {
    font-size: 18px;
    line-height: 27px;
    color: #242424;
    margin-bottom: 16px; }
  .search__img {
    position: absolute;
    top: 8px;
    right: 0;
    width: 380px;
    height: 260px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url(../img/s_image.png); }
  .search .form__item {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 16px;
    width: 522px;
    padding: 16px;
    background-color: #fff;
    border-radius: 12px; }
  .search .form__input {
    width: 450px;
    height: 24px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #91A0B6; }
  .search .form ::-webkit-input-placeholder {
    color: #91A0B6; }
  .search .form ::-moz-placeholder {
    color: #91A0B6; }
  .search .form :-ms-input-placeholder {
    color: #91A0B6; }
  .search .form :-moz-placeholder {
    color: #91A0B6; }
  .search .form__btn {
    position: absolute;
    z-index: 1;
    right: 16px;
    top: 50%;
    margin-top: -12px;
    width: 24px;
    height: 24px;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url(/src/img/icons/i_search_btn.svg);
    cursor: pointer;
    transition: 0.15s; }
    .search .form__btn:hover {
      transform: scale(1.15); }
    .search .form__btn:active {
      transform: scale(0.95); }

.post__date {
  font-size: 16px;
  line-height: 24px;
  color: #78787A;
  margin-bottom: 8px; }

.post__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #242424;
  margin-bottom: 8px; }

.post__text {
  font-size: 16px;
  line-height: 24px;
  color: #78787A; }

.post__img {
  display: block;
  width: auto;
  max-width: 100%;
  margin-bottom: 16px; }

.label {
  background: #ED685C;
  padding: 8px 16px;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 16px;
  display: inline-block;
  border-radius: 4px; }
  .label::after {
    content: "";
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-right: 10px solid #f6f6f6;
    right: -1px;
    top: 0;
    position: absolute; }

.main-post {
  margin-top: 48px; }
  .main-post .post {
    display: flex; }
    .main-post .post__title {
      font-size: 48px;
      line-height: 72px; }
    .main-post .post__img {
      width: 521px;
      margin-right: 32px; }
    .main-post .post__text {
      margin-bottom: 23px; }

.post-list {
  border-top: 1px solid #C4C4C4;
  padding-top: 16px;
  margin-top: 56px; }
  .post-list_double .post {
    width: calc(50% - 16px); }
    .post-list_double .post__img {
      margin: 16px 0px; }
  .post-list_third .post {
    width: calc(33.3333% - 21px);
    margin-bottom: 56px; }
  .post-list_radius .post__img {
    border-radius: 12px; }
  .post-list__title {
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: #91A0B6;
    font-weight: 700;
    margin-bottom: 16px; }
  .post-list__cont {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }

.baner {
  background: rgba(140, 222, 203, 0.832667);
  background: linear-gradient(90deg, #CFEDE6 0%, rgba(140, 222, 203, 0.832667) 100%);
  border-radius: 16px;
  padding: 24px;
  margin: 0 0 56px 0; }
  .baner .logo {
    width: 107px;
    height: 24px;
    margin-bottom: 24px; }
  .baner__title {
    color: #04BF94;
    margin-bottom: 8px;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px; }
  .baner__text {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #000000;
    margin-bottom: 24px; }
  .baner__img {
    position: absolute;
    top: -9px;
    right: -15px;
    width: 345px;
    height: 261px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url(../img/Frame-1273.png); }

.btn {
  padding: 8px 32px;
  background: #04BF94;
  border-radius: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #fff; }

.loading__img {
  width: 1074px;
  margin: 56px 183px 132px; }

@media screen and (max-width: 1200px) {
  .wrapper {
    max-width: 100%;
    padding: 0 16px; }
  .breadcrumbs__item::after {
    display: none; }
  .breadcrumbs__item::before {
    content: '';
    display: none;
    width: 24px;
    height: 24px;
    margin: 0 8px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    transform: rotateY(180deg);
    background-image: url(../img/icons/i_breadcrumbs_arrow.svg); }
  .breadcrumbs__item:first-child:before {
    display: block;
    margin-left: 0; }
  .breadcrumbs__item:last-child {
    display: none; }
  .search {
    padding: 24px 16px 18px; }
    .search__title {
      font-size: 24px;
      line-height: 36px; }
    .search__img {
      display: none; }
    .search .form__item {
      width: 100%; }
  .main-post .post {
    display: block; }
    .main-post .post__title {
      font-size: 24px;
      line-height: 36px; }
  .post-list_double .post {
    width: 100%; }
  .post-list_third .post {
    width: 100%; }
  .baner {
    width: 100%; }
    .baner__img {
      display: none; }
    .baner__titile {
      font-size: 24px;
      line-height: 36px; }
    .baner__text {
      font-size: 18px;
      line-height: 27px; }
  .btn {
    width: 100%;
    display: block;
    text-align: center; } }

.header {
  background-color: #FFFFFF;
